export default {
    data() {
        return {
			type:'1',
			state:'1', //1管理， 2完成
			passengerList:[],
			addressList:[],
			userId:'',
		}
    },
	onLoad(){
		uni.getStorage({
			key:'userInfo',
			fail() {
				uni.showToast({
					icon:'none',
					title:'暂未登录,请登录后查看'
				})
				setTimeout(function(){
					uni.navigateTo({	
						//loginType=1,泉运登录界面
						//loginType=2,今点通登录界面
						//loginType=3,武夷股份登录界面
						url  : '/pages/GRZX/userLogin?loginType=1'
					}) 
				},500);
			}
		})
	},
	// ---------下拉刷新---------
	onPullDownRefresh:function(){
	  this.loadData();
	},
	onShow(){
		//uni.startPullDownRefresh();
		this.loadData();
	},
    methods: {	
		// ---------加载数据---------
		async loadData(){
			var array=[];
			var list=[];
			var that=this;
			uni.getStorage({
				key:"passengerList",
				success(res2) {
					for(var j=0;j<res2.data.length;j++){
						list.push(res2.data[j].passengerId);
					}
				}
			})
			uni.getStorage({
				key:'userInfo',
				success(res){
					that.userId=res.data.userId;
					uni.request({
						//url:'http://111.231.109.113:8002/api/person/userInfoList',
						url:that.$GrzxInter.Interface.userInfoList.value,
						data:{
							userId:res.data.userId
						},
						method:that.$GrzxInter.Interface.userInfoList.method,
						success(res1) {
							console.log(res1,'111')
							for(var i=0;i<res1.data.data.length;i++){
								if(res1.data.data[i].userSex==0){
									res1.data.data[i].userSex="男";
								}else{
									res1.data.data[i].userSex="女";
								}
								res1.data.data[i].deleteIndex=0;
								var data1=res1.data.data[i];
								data1.hiddenIndex=0;
								for(var q=0;q<list.length;q++){
									if(data1.passengerId==list[q]){
										data1.hiddenIndex=1;
									}
								}
								array.push(data1);
							}
							var list1=[];
							for(var i=0;i<array.length;i++){
								if(array[i].hiddenIndex==1){
									list1.push(array[i]);
								}
							}
							uni.stopPullDownRefresh();
							uni.setStorage({
								key:'passengerList',
								data:list1,
							})
						}
					})
				},
				fail() {
					
				}
			})
			// var address=[];
			// uni.getStorage({
			// 	key:'addressList',
			// 	success(res1) {
			// 		console.log(res1)
			// 		for(var i=0;i<res1.data.length;i++){
			// 			address.push(res1.data[i]);
			// 		}
			// 	}
			// })
			// console.log(array.length,"array193")
			// console.log(array,"array")
			// var list1=[];
			// for(var i=0;i<array.length;i++){
			// 	if(array[i].hiddenIndex==1){
			// 		list1.push(array[i]);
			// 	}
			// }
	
			this.passengerList=array;
			// this.addressList=address;
		},
		//---------乘车人管理---------
		passengerClick(){
			this.type=1;
		},
		addressClick(){
			this.type=2;
		},
		// ---------编辑乘车人---------
        editPassenger(e){  
        	uni.setStorage({
        		key:'editPassenger',
        		data:e
        	})
        	uni.navigateTo({
        		url:'/pages/GRZX/addPassenger?type=edit'
        	})
        },
		// ---------添加乘车人---------
		addPassenger(){
			uni.getStorage({
				key:'userInfo',
				fail:function(){
					uni.showToast({
						icon:'none',
						title:'未登录无法添加乘车人,请先登录'
					})
				},
				success:function(res){
					uni.navigateTo({
						url:'/pages/GRZX/addPassenger?type=ad'
					})
				}
			})
		},
		// ---------地址管理---------
		addAddress(){
			uni.redirectTo({
				url:'/pages/GRZX/addAddress?type=add'
			})
		},
		// ---------选择地址---------
		chooseAddress(e){
			// uni.setStorage({
			// 	key:'chooseAddress',
			// 	data:e
			// })
			console.log(2222)
		},
		// ---------编辑地址---------
		editAddress(e){   
		//console.log(3333)
			uni.setStorage({
				key:'editAddress',
				data:e
			})
			uni.redirectTo({
				url:'/pages/GRZX/addAddress?type=edit'
			})
		},
		// ---------删除乘车人信息---------
		deletePassenger(){ 
			var data=this.passengerList;
			var that=this;
			// uni.removeStorage({
			//     key: 'passengerList',
			//     success: function (res) {
			//         //console.log('success');
			//     }
			// });
			var deleteList=[];
			for(var i=0;i<data.length;i++){
				if(data[i].deleteIndex==1){
					deleteList.push(data[i]);
				}
			}
			console.log(deleteList,"deleteList")
			if(deleteList.length==0){
				uni.showToast({
					title: '请选择',
					icon:"none"
				})
			}else{
				for(var j=0;j<deleteList.length;j++){
					uni.request({
						// url:'http://111.231.109.113:8002/api/person/deletuserInfoList',
						url:that.$GrzxInter.Interface.deletuserInfoList.value,
						data:{
							userId:that.userId,
							passengerId:deleteList[j].passengerId,
						},
						method:that.$GrzxInter.Interface.deletuserInfoList.method,
						success(res) {
							//console.log(res,"res")
						}
					})	
				}
				this.state=1;
				uni.redirectTo({
					url:'/pages/GRZX/infoList'
				})
			}
		},
		// ---------删除地址---------
		deleteAddress(){  //
			var data=this.addressList;
			var array=[];
			for(var i=0;i<data.length;i++){
				if(data[i].deleteIndex==0){
					array.push(data[i]);
				}
			}
			if(array.length==0){
				uni.showToast({
					title: '请选择',
					icon:"none"
				})
			}else{
				uni.setStorage({
					key:"addressList",
					data:array
				})	
			}
			this.state=1;
			uni.redirectTo({
				url:'/pages/GRZX/infoList'
			})
		},
		// ---------选中---------
		selete(e){
			if(e.deleteIndex==0){
				e.deleteIndex=1;
			}else{
				e.deleteIndex=0;
			}
		},
		// ---------返回按钮---------
		returnClick(){
			uni.switchTab({
				url:'/pages/GRZX/user'
			})
		},
		// ---------删除乘车人---------
		deleteClick(){
			var that=this;
			uni.getStorage({
				key:'userInfo',
				fail:function(){
					uni.showToast({
						icon:'none',
						title:'未登录无法管理,请先登录'
					})
				},
				success:function(res){
					that.state=2;
				}
			})
		},
		// ---------完成---------
		finishClick(){
			this.state=1;
		},
		checkClick(){
			this.checkState=1;
		},
		uncheckClick(){
			this.checkState=0;
		},
    }
};
